import { adapters } from "@rp/adapters"
import {
  BriefTapToExploreInteraction,
  ExternalToolsBriefSourceInteractionMessage,
} from "./customerIOBriefEvents.types"

export const sendBriefSourceInteraction = (
  data: ExternalToolsBriefSourceInteractionMessage["data"],
) => {
  const message: ExternalToolsBriefSourceInteractionMessage = {
    name: "briefSource",
    data,
  }
  adapters.customerIo.track(message)
  return message
}

